@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}
:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --prmary-red: #ef4035;
  --primary-hover-red: #f33501;
}
body {
  font-family: "Roboto Condesed", sans-serif;
}

nav {
  z-index: 2;
  padding: 0 !important;
}
.logo {
  width: 10.25rem;
}

/*********manu link*******/

nav a.nav-link {
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}
.navbar-nav {
  margin-left: auto;
}
.navbar-nav > li > a {
  margin-left: 0.75rem;
  margin-right: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-bottom: 0.1875rem solid rgba(255, 255, 255, 0);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem;
}

@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}
.navbar-nav > li > a.active,
.navbar-nav > li > a:focus {
  border-bottom: 0.1875rem solid #f9ab00;
  border-bottom: 0.1875rem solid var(--primary-orange);
}
.navbar-nav > li > a:hover {
  border-bottom: 0.1875rem solid #f9ab00;
  border-bottom: 0.1875rem solid var(--primary-orange);
}

/*********Header***********/

.header-wrapper {
  position: relative;
  background: url(/my-portfolio/static/media/laptop2.c7675080.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: -webkit-flex;
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  font-family: "Roboto Condesed", sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}

/*************typed text******/
.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}
@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}
/*******Header Button*******/
.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--prmary-red);
  text-transform: uppercase;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  border-radius: 0;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
  text-decoration: none;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #ef4035;
  background-color: var(--prmary-red);
  color: #fff;
  color: var(--primary-white);
  transition: 0.2s ease-in-out;
}

/*******particles*******/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

.title {
  color: #f9ab00 !important;
  color: var(--primary-orange) !important;
}

.aboutme-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.imglogo{
  border-radius: 50%;
  width: 70%;

  border: 3px solid #f9ab00;
}

.skill-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 55px 0;
}

.description {
  text-align: justify;
}

/* Skils sections */

.skills-main {
  width: 35vw;
}
.skill-list-ul {
  list-style-type: none;
}
.skill-item {
  width: 25vw;
  background: beige;
  border: 1px solid;
  position: relative;
  margin-top: 2px;
  margin-bottom: 10px;
  border-radius: 20px;
  height: 10px;
}
.skill-title {
  margin-left: 15px;
  font-weight: bold;
}
.skill-item-container {
  margin: 35px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}
.skill-fill {
  background: #8a2be2;
  position: absolute;
  height: 10px;
  border-radius: 20px;
  bottom: -1px;
  left: -1px;
}
.html {
  width: 90%;
  -webkit-animation: html 3s;
          animation: html 3s;
}
@-webkit-keyframes html {
  0% {
    width: 0%;
  }
  90% {
    width: 90%;
  }
}
@keyframes html {
  0% {
    width: 0%;
  }
  90% {
    width: 90%;
  }
}

.js {
  width: 80%;
  -webkit-animation: js 6s;
          animation: js 6s;
}
@-webkit-keyframes js {
  0% {
    width: 0%;
  }
  90% {
    width: 80%;
  }
}
@keyframes js {
  0% {
    width: 0%;
  }
  90% {
    width: 80%;
  }
}

.react {
  width: 60%;
  -webkit-animation: react 9s;
          animation: react 9s;
}
@-webkit-keyframes react {
  0% {
    width: 0%;
  }
  90% {
    width: 60%;
  }
}
@keyframes react {
  0% {
    width: 0%;
  }
  90% {
    width: 60%;
  }
}

.nodejs {
  width: 50%;
  -webkit-animation: nodejs 12s;
          animation: nodejs 12s;
}
@-webkit-keyframes nodejs {
  0% {
    width: 0%;
  }
  90% {
    width: 50%;
  }
}
@keyframes nodejs {
  0% {
    width: 0%;
  }
  90% {
    width: 50%;
  }
}
